@import url('https://fonts.googleapis.com/css?family=Great+Vibes');


body * {
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

body {
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-image:url(./assets/images/background-image.jpeg);
    background-size: cover;
    padding: 30px 0 0 0;
    position: relative;
    min-height: 100vh;
    max-height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    

}

body::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    background: rgba(14, 13, 17, 0.92);
}

.container {
    width: 300px;
    /* margin: 0 auto; */
    padding: 30px 30px 30px 30px;
    border-radius: 30px;
    border:1px solid rgba(255, 255, 255, 0.24);
    /* height: 80vh; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    overflow: hidden;
    background: rgba(14, 13, 17, 0.82);
}

.container h1 {
    font-family: 'Great Vibes', cursive;
    color:rgb(255, 255, 255);
    font-size: 40px;
    margin-bottom: 30px;
}

.container input, .container button {
    padding: 10px;
    border:none;
    outline: none;
    margin-bottom: 30px;

}

.container button {
    background-color: #ffffff;
    color: rgba(0, 0, 0, 0.5);
    cursor: pointer;
    border-radius: 20px;
    min-width: 50%;
}

.container button:hover {
    background-color: #7af334;
    color: rgba(0, 0, 0, 0.7);
    letter-spacing: 1px;
}

.container input:focus,.container button:hover  {
    -webkit-box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.7);
            box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.7);
}

.container input {
    width: 80%;
    background-color: transparent;
    border: 1px solid #fff;
    color: #fff;
}
.container input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.377);
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}
.container input::-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.377);
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}
.container input::placeholder {
    color: rgba(255, 255, 255, 0.377);
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.container input:focus {
    width: 90%;
}

.container input:focus::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.05)
}

.container input:focus::-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.05)
}

.container input:focus::placeholder {
    color: rgba(255, 255, 255, 0.05)
}

.container p {
    display: inline-block;
    /* padding: 0 10px; */
    font-family: 'Josefin Sans', sans-serif;
    color: rgba(253, 253, 253, 0.514);
    

}

p.city {
    font-size: 20px;
    color: #fff;
}

.weather-icon__container {
margin: 20px 0;
}

.weather-icon__container > img {
    width: 60%;
}

.temperature__container {
    /* background-color: rgba(255, 255, 255, 0.342); */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}
.temperature-icon {
    height: 20px;
    padding: 0 10px 0 0;
}

p.temperature::after {
    content: url("./assets/SVG/Asset 37.svg");
    width: 15px;
    height: 15px;
    display: inline-block;
}




